body {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  text-decoration: none;
  font-family: 'Roboto', sans-serif;
}

.app {
  min-height: 100vh;
  outline: none;
}

.app_title {
  font-size: 22px;
  margin-left: 30px;
}

.belt_active, .belt_inactive, .belt_stoped {
  height: 50px;
  border-radius: 8px;
  border: 5px solid transparent;
  width: 300px;
  cursor: pointer;
}

.belt_active {
  background: rgb(64, 230, 130);
}

.belt_inactive {
  background: rgb(220, 59, 19);
}

.belt_stoped {
  background: rgb(135, 133, 132);
}

.belt_block {
  display: flex;
  padding-top: 15px;
  margin-top: 10px;
}

.belt_main {
  margin-left: 30px;
  min-width: 700px;
  margin-top: 25px;
}

.belt_title {
  margin-left: 15px;
  padding-top: 17px;
}

.selected {
  border: 5px solid rgb(0, 13, 255)
}

.buttons_button {
  border: none;
  background: rgb(229, 236, 236);
  width: 600px;
  height: 70px;
  color: #000;
  border-radius: 8px;
  border: 2px solid rgb(171, 164, 164);
  font-family: 'Roboto', sans-serif;
  font-size: 20px;
  text-transform: uppercase;
  margin-top: 20px;
  outline: none;
  transition: transform 0.1s;
}

.buttons_button:hover {
  transform: scale(1.07);
}

.buttons_container {
  display: flex;
  flex-direction: column;
  margin-left: 7%;
}

.button_log {
  border: none;
  background: rgb(229, 236, 236);
  width: 300px;
  height: 70px;
  color: #000;
  border-radius: 8px;
  border: 2px solid rgb(171, 164, 164);
  font-family: 'Roboto', sans-serif;
  font-size: 20px;
  text-transform: uppercase;
  margin-top: 40px;
  outline: none;
  transition: transform 0.1s;
  margin-top: 17%;
  margin-left: 25%;
}

.button_log:hover {
  transform: scale(1.07);
}
.wrapper {
  display: flex;
}

@media screen and (max-width: 830px) {
  .app {
    flex-direction: column;
  }
}